import Link from 'next/link';
import { useRouter } from 'next/router';
import { useAuth } from '../context/AuthContext';

/**
 * Layout component that wraps authenticated pages. It provides a sidebar
 * navigation, a header with user information and a logout button, and a
 * content area. The navigation highlights the active route and adapts to
 * whether the user is an admin.
 */
export default function Layout({ children }) {
  const { user, logout } = useAuth();
  const router = useRouter();

  // Define navigation items available to all users
  const nav = [
    { href: '/dashboard', label: 'Dashboard' },
    { href: '/hashtag-discovery', label: 'Hashtag Discovery' },
    { href: '/post-scheduler', label: 'Post Scheduler' },
    { href: '/dm-templates', label: 'DM Automation' },
    { href: '/subscription', label: 'Subscription' },
    { href: '/credits', label: 'Credits' },
    { href: '/account', label: 'Account Settings' },
  ];
  // Add admin navigation if applicable
  if (user && user.role === 'admin') {
    nav.push({ href: '/admin/users', label: 'Admin Users' });
  }

  return (
    <div className="flex min-h-screen">
      {/* Sidebar */}
      <aside className="w-60 bg-white border-r border-gray-200 hidden sm:block">
        <div className="p-6 border-b border-gray-200">
          <h2 className="text-2xl font-semibold text-gray-800">LeadGen SaaS</h2>
        </div>
        <nav className="p-4 space-y-2">
          {nav.map((item) => {
            const active = router.pathname === item.href;
            return (
              <Link key={item.href} href={item.href} legacyBehavior>
                <a
                  className={`block px-3 py-2 rounded-md text-sm font-medium hover:bg-gray-100 ${
                    active ? 'bg-gray-100 text-blue-600' : 'text-gray-700'
                  }`}
                >
                  {item.label}
                </a>
              </Link>
            );
          })}
        </nav>
      </aside>
      {/* Main content with header */}
      <div className="flex-1 flex flex-col">
        <header className="bg-white shadow-sm border-b border-gray-200">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4 flex items-center justify-between">
            <div>
              <h1 className="text-xl font-semibold text-gray-900">{nav.find((n) => n.href === router.pathname)?.label || ''}</h1>
            </div>
            {/* User info and logout */}
            {user && (
              <div className="flex items-center space-x-4">
                <div className="text-right">
                  <p className="text-sm font-medium text-gray-700">{user.email}</p>
                  <p className="text-xs text-gray-500">Credits: {user.credits ?? 0}</p>
                  <p className="text-xs text-gray-500">Subscription: {user.subscriptionStatus || 'none'}</p>
                </div>
                <button
                  onClick={logout}
                  className="px-3 py-2 text-sm text-white bg-blue-600 rounded hover:bg-blue-700"
                >
                  Logout
                </button>
              </div>
            )}
          </div>
        </header>
        <main className="flex-1 bg-gray-50 p-4 overflow-y-auto">{children}</main>
      </div>
    </div>
  );
}
